// import webJsSDK from '@hk01-digital/web-js-sdk'
// import webJsSDKStg from '@hk01-digital/web-js-sdk/dist/stg/esm'
import * as R from 'ramda'
import { isBrowserEnv } from 'src/utils/browser'
import uuidStorage from 'src/utils/uuidStorage'
import { v4 as uuidv4 } from 'uuid'

export const SESSION_KEYS = {
  SESSION_ID: 'hk01_session_id'
}

const DEFAULT_COOKIE_TIMEOUT = 60 * 30 // 30 mins expire time

const cachedSessionId = uuidStorage.readFromCookie(
  SESSION_KEYS.SESSION_ID,
  null
)

const initConfig = {
  appId: 'B9SG6A7VU7J.com.hk01.news-app.prd',
  appVersion: '1.0.1',
  baseApiUri: 'https://proxy-sso-domain/api/v1.0',
  service: 'e',
  anonymousId: 'e905c8bb-e561-497d-85c9-b24100f83d64',
  sessionId: cachedSessionId
}

class WebJsSdkService {
  sdk
  trackerOpts
  constructor(trackerOpts = {}) {
    this.trackerOpts = trackerOpts

    this.sdk = null
  }

  initSdk = async () => {
    const isBrowser = isBrowserEnv()

    // const WebJsSdk = isBrowser
    //   ? process.env.APP_ENV === 'production'
    //     ? webJsSDK
    //     : webJsSDKStg
    //   : null

    const WebJsSdk = isBrowser
      ? process.env.APP_ENV === 'production'
        ? require('@hk01-digital/web-js-sdk').default
        : require('@hk01-digital/web-js-sdk/dist/stg/esm').default
      : null

    if (!initConfig.sessionId) {
      const newId = uuidv4()

      initConfig.sessionId = newId

      uuidStorage.saveToCookie(
        SESSION_KEYS.SESSION_ID,
        newId,
        DEFAULT_COOKIE_TIMEOUT
      )
    }

    if (!isBrowser) {
      return null
    }

    const sdk = new WebJsSdk(initConfig, {
      webviewBridgeEnabled: true,
      GA: {
        trackingId: 'UA-70981149-1'
        // or trackingId: ['UA-70981149-1', 'G-58EHTZMCKL']
        // details see https://github.com/hk01-digital/js-aux/tree/development/packages/data-tracker-client#supporting-both-ua-and-ga4
      },
      Piwik: {
        trackingUrl: 'https://track.hktester.com/v2/piwik.php',
        siteId: 5, // replace with the siteId of your web application
        userId: 'e905c8bb-e561-497d-85c9-b24100f83d64',
        isSPA: true
      },
      ...this.trackerOpts
    })

    try {
      await sdk.init()
    } catch (error) {
      throw new Error(`{error on jssdk init | ${R.toLower(error.message)}}`)
    }

    return sdk
  }

  getSdk = async () => {
    if (!this.sdk) {
      this.sdk = await this.initSdk()
    }

    return this.sdk
  }

  isWebview = async () => {
    const sdk = await this.getSdk()

    return sdk.getPlatform() === 'webview'
  }

  hideHeader = async () => {
    const sdk = await this.getSdk()

    sdk.app.hideHeader()
  }

  showHeader = async () => {
    const sdk = await this.getSdk()

    sdk.app.showHeader()
  }

  getDeviceId = async () => {
    const sdk = await this.getSdk()

    return sdk.app.getDeviceId()
  }

  getPlatform = async () => {
    const sdk = await this.getSdk()

    return sdk.app.getPlatform()
  }

  fireGtmEvent = async (event, params) => {
    const sdk = await this.getSdk()

    sdk.app.fireGtmEvent(event, params)
  }

  dismissWebview = async () => {
    const sdk = await this.getSdk()

    sdk.app.dismissWebview()
  }

  goToURL = async url => {
    const sdk = await this.getSdk()

    sdk.app.goTo(url)
  }

  openNativeInAppBrowser = async url => {
    const sdk = await this.getSdk()

    return sdk.app.openNativeInAppBrowser(url)
  }

  goTo = async (url: string, target?: string) => {
    const sdk = await this.getSdk()
    const hostname = new URL(url).hostname

    if (
      sdk.isPlatform(sdk.PLATFORM.WEBVIEW) &&
      !R.equals(hostname, window.location.hostname)
    ) {
      sdk.app.goTo(url)
    } else {
      window.open(url, target || '_blank')
    }
  }
}

export default WebJsSdkService
