import { isBrowserEnv } from './browser'

export default {
  // session storage
  readFromSession: (key, defaultValue = null) => {
    if (!key) {
      return defaultValue
    }

    if (!isBrowserEnv() || !window.sessionStorage) {
      return defaultValue
    }

    const value = window.sessionStorage.getItem(key)

    if (value === null) {
      return defaultValue
    }

    return value
  },

  saveToSession: (key, value) => {
    if (!key) {
      return false
    }

    if (!isBrowserEnv() || !window.sessionStorage) {
      return false
    }

    window.sessionStorage.setItem(key, value)

    return true
  },

  clearSession: () => {
    if (process?.browser && window.sessionStorage) {
      window.sessionStorage.clear()
    }
  },

  // cookie
  readFromCookie: (key, defaultValue = null) => {
    if (!key) {
      return defaultValue
    }

    if (!isBrowserEnv() || !document.cookie) {
      return defaultValue
    }

    const name = key + '='

    const decodedCookie = decodeURIComponent(document.cookie)

    const possibleCookies = decodedCookie
      .split(';')
      .filter(item => item.includes(name))

    if (possibleCookies.length === 0) {
      return defaultValue
    }

    const cookieValue = possibleCookies[0].split(name).join('')

    return cookieValue
  },

  saveToCookie: (key, value, maxAge) => {
    if (!key) {
      return false
    }

    if (!isBrowserEnv()) {
      return false
    }

    document.cookie = key + '=' + value + '; max-age=' + maxAge + ';path=/'

    return true
  }
}
